
* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;

}

input {
  color: green;
  font-size: 1.2rem;
  width: 100%;
}


.carta-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.carta {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.carta p {
  padding: 5px;
  text-align: center;
}

.carta img {
  max-height: 170px;
}
